* {
  --headerBackground: white;
  --bodyBackground: #e6e6e6;
  --cardBackground: white;
  --textColor: black;
  margin: 0;
  padding: 0;
  font-size: 1rem;
}

.header {
  color: var(--textColor);
  background-color: var(--headerBackground);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem;
  display: flex;
  position: fixed;
  top: 0;
}

.logo {
  font-family: monospace;
  font-size: 2rem;
  font-weight: bold;
}

.navItems {
  margin: 0 1rem;
  list-style-type: none;
  display: flex;
}

.navItems li {
  white-space: nowrap;
  padding: .5rem 1.5rem;
}

.body {
  background-color: var(--bodyBackground);
  margin-top: 5rem;
}

.foodContainer {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.foodCard {
  background-color: var(--cardBackground);
  color: var(--textColor);
  border-radius: 7px;
  width: 15rem;
  min-width: 15rem;
  height: 20rem;
  margin: 1.5rem;
}

.foodCard:hover {
  transition: all .3s;
  scale: 1.1;
  box-shadow: 1rem 1rem 2rem gray;

  & .storeImg {
    transition: all .3s;
    scale: 1.05;
  }
}

.foodCard:not(:hover) {
  transition: all .2s;

  & .storeImg {
    transition: all .2s;
  }
}

.storeImg {
  object-fit: cover;
  border-radius: 7px;
  width: 15rem;
  height: 10rem;
  margin-bottom: 1rem;
}

.foodCard div {
  margin-top: 3px;
  padding-left: 1rem;
}

.storeName {
  margin-bottom: 7px;
  font-weight: 600;
}

.foodItems {
  color: gray;
}

.storeRating {
  color: green;
}

.veg {
  white-space: nowrap;
  color: green;
  border: 1px solid green;
  border-radius: 5px;
  width: fit-content;
  padding: 0 5px;
}

.nonVeg {
  white-space: nowrap;
  text-align: center;
  color: red;
  border: 1px solid red;
  border-radius: 5px;
  width: fit-content;
  margin-top: 5rem;
  padding: 0 5px;
}
